@import url(https://fonts.googleapis.com/css2?family=Urbanist:wght@400;700&display=swap);
body {
  background-color: #e8e8e8;
  font-family: 'Urbanist', sans-serif;
  color: #717171;
  /* color: #8f8f8f; */
}

/* loading page */

.loading {
  display: flex;
  align-items: center;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.video-icon {
  text-align: center;
  margin-right: 10px;
}

/* movie */

.movie-container {
  background-color: #fff;
  border-radius: 8px;
}

.movie-title {
  color: #717171;
  margin-bottom: 10px;
}

.movie-genres {
  display: flex;
  color: #ababab;
}

.movie-poster {
  transition: ease, 0.5s;
}

.movie-container:hover .movie-poster{
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}


@media screen and (min-width: 320px) {
  .movie-container {
    width: 300px;
    height: auto;
    margin: 10px auto;
    padding: 20px 10px;
    background-color: #fff;
  }
  
  .movie-poster {
    display: block;
    width: 200px;
    height: auto;
    margin: 0 auto 30px;
    box-shadow: 3px 5px 5px #a7a7a7;
  }

  .video-icon {
    font-size: 40px;
  }

  .loading p{
    font-size: 20px;
  }

  .movie-year,  
  .movie-rating,
  .movie-runtime {
    display: block;
    margin-bottom: 7px;
  }


  .movie-title {
    font-size: 20px;
  }

  .movie-year {
    display: block;
    font-size: 17px;
  }

  .movie-genres {
    flex-wrap: wrap;
    width: 250px;
    font-size: 15px;
  }

  .movie-genre {
    margin: 0 7px 7px 0;
  }

  .movie-summary {
    height: 49px;
    overflow: hidden;
  }

  @media screen and (min-width: 768px) {
    .movie-container {
      display: flex;
      width: 70%;
      height: 270px;
      margin: 40px auto;
    }

    .movie-description {
      display: flex;
      flex-direction: column;
    }

    .movie-poster {
      width: 170px;
      margin: 0 20px 0 0;
    }

    .movie-title {
      font-size: 20px;
    }

    .movie-genres {
      width: 200px;
      margin-bottom: 10px;
    }

    .movie-summary {
      height: 60px;
    }

    .movie-title,
    .movie-summary {
      line-height: 1.3;
      padding-right: 25px;
    }

  }

  @media screen and (min-width: 1024px) {
    .box {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      padding-left: 20px;
    }

    .movie-container {
      width: 500px;
      height: 300px;
      margin: 40px 10px;
    }

    .movie-poster {
      width: auto;
    }
  }

}
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
* {
  box-sizing: border-box;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  /* font: inherit; */
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
input {
  border: none;
}

